<template>
  <a-layout style="min-height: 100vh">
    <sidebar/>
    <a-layout>
      <topbar/>
      <a-layout-content :style="{ padding: '0 50px', marginTop: '64px',marginLeft:'200px' }">
        <router-view />
      </a-layout-content>
      <div class="float-btn">
        <open-billboard-btn/>
      </div>
    </a-layout>
  </a-layout>
</template>

<script lang="ts">
import Sidebar from '@/components/Sidebar.vue'
import Topbar from '@/components/Topbar.vue';
import OpenBillboardBtn from '@/views/Billboard/OpenBillboardBtn.vue';
import { defineComponent, onMounted, ref } from 'vue';
import { useStore } from '@/store';
import {useRouter} from 'vue-router';

export default defineComponent({
  components: {
    Sidebar,
    Topbar,
    OpenBillboardBtn,
  },
  setup() {
    const store = useStore();
    const router = useRouter();
    
    console.log(store.state.user.username)

    const initHub = () =>{
      const url = new URL('/api/v1/billboard/ws/', window.location.href);
      url.protocol = url.protocol.replace('http', 'ws');
      const targetUrl = `${url.href}?name=${store.state.user.username}`
      store.commit('billboard/WSCONN_INIT',targetUrl)
    }

    onMounted(()=>{
      if(!store.state.user.accessToken||!store.state.user.username){
        router.push({"name":"Login",query:{redirect:router.currentRoute.value.fullPath}})
      }
      initHub()
    })

    return {
      collapsed: ref<boolean>(false),
      selectedKeys: ref<string[]>(['1']),
    };
  },
});
</script>

<style lang="scss">
.float-btn{
  position: fixed;
  bottom: 24px;
  right: 24px;
}
</style>