<template>
  <div class="page">
    <div class="container">
      <a-row class="login-panel" justify="center" align="middle">
        <a-col :span="20" :md="8" :lg="6">
          <a-card title="Login">
            <a-form :model="loginForm">
              <a-form-item label="Username">
                <a-input v-model:value="loginForm.username" />
              </a-form-item>
              <a-form-item  label="Password">
                <a-input-password v-model:value="loginForm.password" />
              </a-form-item>
              <a-form-item>
                <a-button
                  type="primary"
                  html-type="submit"
                  :disabled="loginForm.name === '' || loginForm.password === ''"
                  @click="loginSumbitBtnHandler"
                >
                  Log in
                </a-button>
              </a-form-item>
            </a-form>
          </a-card>
        </a-col> </a-row
      >
    </div>
  </div>
</template>


<script lang="ts">
import {LoginAPI} from '@/api/user';

import { defineComponent, ref } from "vue";
import {useStore} from '@/store/index';
import {useRouter} from 'vue-router';
import { user } from '@/typings/user';

import { createToast } from "mosha-vue-toastify";
import "mosha-vue-toastify/dist/style.css";


export default defineComponent({
  setup() {
    const store = useStore();
    const router = useRouter();
    const loginForm = ref<user>({username:'',password:''})

    const loginSumbitBtnHandler = async ()=>{
      const res = await LoginAPI(loginForm.value)
      if (res){
        const data = res.data.data
        store.commit("user/USERNAME_SET",data.username)
        store.commit("user/ACCESSTOKEN_SET",data.accessToken)
        store.commit("billboard/ACCESSTOKEN_SET",data.accessToken)
        let redirect = router.currentRoute.value.query.redirect
        if(redirect){
          router.push({'path':redirect.toString()})
        }else {
          router.push({'name':'Home'})
        }
      }else{
        createToast("Wrong Password",{
          "type":"danger",
          "position":"bottom-right",
          "showIcon":true
        })
      }
    }
    return {
      loginForm,
      loginSumbitBtnHandler
    };
  },
});
</script>


<style lang="scss" scoped>
.page{
  .container{
    height: 90vh;
    .login-panel{
      height: 100%;
    }
  }
}
</style>