import service from "@/utils/request";
import { IResponse, LoginRespone } from "@/typings/response";
import { AxiosPromise } from "axios";
import { user } from "@/typings/user";

// @Summary 用户登录
// @Produce  application/json
// @Router /token/ [post]
export function LoginAPI(loginForm:user): AxiosPromise<IResponse<LoginRespone>> {
  return service({
    url: 'token/',
    method: "post",
    data:loginForm,
  });
}