<template>
  <div class="billboard">
    <div class="console-panel">
      <div class="console" ref="term"></div>
    </div>
    <div class="input-panel">
      <a-form :model="sendForm">
        <a-form-item title="Chat">
          <a-input-search
            v-model:value="sendForm.detail"
            placeholder="Chat Here"
            enter-button="send"
            @search="sendMessageBtnHandler"
          />
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, onUnmounted, Ref, ref, watch } from "vue";
import "xterm/css/xterm.css";
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
import { WSData, WSDataType } from "@/typings/billboard";
import { useStore } from "@/store";
import moment from "moment";

const defaultTheme = {
  foreground: "#ffffff", // 字体
  background: "#1b212f", // 背景色
  cursor: "#ffffff", // 设置光标
  selection: "rgba(255, 255, 255, 0.3)",
  black: "#000000",
  brightBlack: "#808080",
  red: "#ce2f2b",
  brightRed: "#f44a47",
  green: "#00b976",
  brightGreen: "#05d289",
  yellow: "#e0d500",
  brightYellow: "#f4f628",
  magenta: "#bd37bc",
  brightMagenta: "#d86cd8",
  blue: "#1d6fca",
  brightBlue: "#358bed",
  cyan: "#00a8cf",
  brightCyan: "#19b8dd",
  white: "#e5e5e5",
  brightWhite: "#ffffff",
};

export default defineComponent({
  props: {
    targetUrl: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const term: Ref<HTMLElement | null> = ref(null);
    const terminal: Ref<Terminal | null> = ref(null);
    const fitAddon: Ref<FitAddon> = ref(new FitAddon());
    const sendForm: Ref<WSData<any>> = ref({
      timestamp: moment().valueOf(),
      sender: "",
      type: WSDataType.MESSAGE,
      data: null,
      detail: "",
    });
    const store = useStore();

    const startTerminal = function (): void {
      console.log(term);
      if (term.value) {
        terminal.value = new Terminal({
          rendererType: "canvas", // 渲染类型
          convertEol: true, // 启用时，光标将设置为下一行的开头
          scrollback: 50, // 终端中的回滚量
          disableStdin: true, // 是否应禁用输入。
          cursorStyle: "bar", // 光标样式
          cursorBlink: true, // 光标闪烁
          theme: defaultTheme,
        });
        terminal.value.loadAddon(fitAddon.value);
        terminal.value.open(term.value);
        ///fitAddon.value.fit();

        window.addEventListener("resize", () => {
          try {
            console.log("fit");
            fitAddon.value.fit();
          } catch (e) {
            console.log(e);
          }
        });
      }
    };

    const sendMessage = (data: WSData<any>) => {
      store.commit("billboard/WSCONN_SEND", data);
    };

    const sendMessageBtnHandler = () => {
      console.log("sending ", sendForm.value);
      sendMessage(sendForm.value);
    };

    const rendWsData = (msg: WSData<any>) => {
      switch (msg.type) {
        case WSDataType.USER:
          terminal.value?.write(
            `[${moment(msg.timestamp).format("MM-DD-HH:mm")}] [${msg.sender}] \x1b[;32m${msg.detail}\x1b[0m\n`
          );
          break;
        case WSDataType.BEACON:
          terminal.value?.write(
            `[${moment(msg.timestamp).format("MM-DD-HH:mm")}] [${msg.sender}] \x1b[;33m${msg.detail}\x1b[0m\n`
          );
          break;
        case WSDataType.LISTENER:
          terminal.value?.write(
            `[${moment(msg.timestamp).format("MM-DD-HH:mm")}] [${msg.sender}] \x1b[;34m${msg.detail}\x1b[0m\n`
          );
          break;
        case WSDataType.MESSAGE:
          terminal.value?.write(`[${moment(msg.timestamp).format("MM-DD-HH:mm")}] [${msg.sender}] ${msg.detail}\n`);
          break;
        case WSDataType.DEBUG:
          console.log(msg);
          break;
        default:
          terminal.value?.write(
            `[${msg.sender}] type:${msg.type} data:${msg.data} detail:${msg.detail}\n`
          );
      }
    };

    const syncBillboard = () => {
      const billboard = store.state.billboard.billboard;
      billboard.forEach((wsData) => {
        rendWsData(wsData)
      });
    };

    watch(
      () => store.state.billboard.incommingWSData,
      (newVal, oldVal) => {
        console.log("new",newVal,"old",oldVal);
        if (newVal!==oldVal && newVal) {
          rendWsData(newVal);
        }
      }
    );

    onMounted(() => {
      startTerminal();
      syncBillboard();
    });

    return {
      term,
      terminal,
      sendForm,
      sendMessage,
      sendMessageBtnHandler,
      startTerminal,
    };
  },
  methods: {},
});
</script>

<style lang="scss" scoped>
.console {
  height: 100%;
  width: 100%;
}
</style>