import { Module } from 'vuex'
import RootState from '@/store/interface'
import BillboardState from './interface'
import { WSData } from '@/typings/billboard'

const billboardModule: Module<BillboardState, RootState> = {
  namespaced: true,
  state: {
    billboard: [],
    incommingWSData:null,
    wsConn: null,
    accessToken:"",
  },
  mutations: {
    RECORD_ADD: (state, record: WSData<any>) => {
      state.billboard.push(record)
    },
    BORAD_CLEAR: (state) => {
      state.billboard = []
    },
    ACCESSTOKEN_CLEARs: (state) => {
      state.accessToken = ""
    },
    ACCESSTOKEN_SET: (state,accessToken) => {
      state.accessToken = accessToken
    },
    ALL_CLEAR:(state) =>{
      state.billboard = []
      state.incommingWSData = null
      state.accessToken = ""
      state.wsConn = null
    },
    WSCONN_INIT: (state,wsConnUrl:string) => {
      state.wsConn = new WebSocket(wsConnUrl)
      state.wsConn.onopen = (e: Event) => {
        console.log("Connected to server");
        if(state.wsConn)
          state.wsConn.send(JSON.stringify({type:"auth",data:state.accessToken}))
      };
      state.wsConn.onmessage = (e: MessageEvent) => {
        const msg:WSData<any> = JSON.parse(e.data);
        state.incommingWSData = msg;
        state.billboard.push(msg)
        /*switch (msg.type){
          case WSDataType.USER:
            terminal.value?.write(`[${msg.sender}] \x1b[;32m${msg.detail}\x1b[0m\n`)
            break
          case WSDataType.MESSAGE:
            terminal.value?.write(`[${msg.sender}] ${msg.detail}\n`)
            break
          case WSDataType.DEBUG:
            console.log(msg)
            break
          default:
            terminal.value?.write(`[${msg.sender}] type:${msg.type} data:${msg.data} detail:${msg.detail}\n`);
        }*/
      };
      state.wsConn.onclose = (e: Event) => {
        console.log("Connection Closed");
        //terminal.value?.setOption('cursorBlink',false);
      };
      state.wsConn.onerror = (e: Event) => {
        console.log("Failed");
      };
    },
    WSCONN_SEND:(state,wsData:WSData<any>)=>{
      state.wsConn?.send(JSON.stringify(wsData))
    }
  }
}

export default billboardModule