<template>
  <a-popover v-model:visible="visible" title="Billboard" trigger="click" placement="topRight">
    <template #content>
      <billboard-widget class="billboard" :targetUrl="targetUrl"/>
      <a @click="hide">Close</a>
    </template>
    <a-button shape="circle"  type="primary">
      <template #icon><MessageOutlined/></template>
    </a-button>
  </a-popover>
</template>

<script lang="ts">
import { defineComponent,onMounted,ref } from "vue";
import { MessageOutlined} from '@ant-design/icons-vue';
import BillboardWidget from "./BillBoardWidget.vue";

export default defineComponent({
  components: {
    BillboardWidget,
    MessageOutlined
  },
  setup() {
    const targetUrl = ref<string>('');
    const visible = ref<boolean>(false);
    const hide = () => {
      visible.value = false;
    };

    const startBillboard = () =>{
      let url = new URL('/api/v1/billboard/ws/?name=a', window.location.href)
      url.protocol = url.protocol.replace('http','ws')
      targetUrl.value = url.href
    }

    onMounted(()=>{
      startBillboard()
    })

    return {
      targetUrl,
      visible,
      hide
    };
  },
});
</script>


<style lang="scss">
.billboard{
  min-height: 240px;
  min-width: 600px;
}
</style>